@import url("https://fonts.googleapis.com/css2?family=JetBrains+Mono&display=swap");
@import url("https://cdn-uicons.flaticon.com/uicons-regular-straight/css/uicons-regular-straight.css");

* {
    padding: 0;
    margin: 0;
    font-family: "JetBrains Mono", monospace;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}

html, body
{
    height: 100%;
    min-height: 100%;
}

.center-items {
    display: flex;
    justify-content: center;
    align-items: center;
}

.center-items-inline {
    display: flex;
    align-items: center;
}

.flex-row {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.flex-column {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.custom-scrollbar {
    &::-webkit-scrollbar {
        width: 5px;
        height: 10px;
        margin-left: 10px;
    }

    &::-webkit-scrollbar-thumb {
        display: none;
    }
}

.shadow {
    -webkit-box-shadow: 0px 7px 10px 4px rgba(0,0,0,0.3);
    -moz-box-shadow: 0px 7px 10px 4px rgba(0,0,0,0.3);
    box-shadow: 0px 7px 10px 4px rgba(0,0,0,0.3);
}

.flex-space-center-wrap {
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
}

.padding-content {
    padding: 1vh 2vw;
    box-sizing: border-box;
}

.border-2px {
    border: 2px solid #000;
}

.btn {
    text-align: center;
    //padding: 0.3vh 1vw;
    padding: 1vh 0.5vh;
    box-sizing: border-box;
    width: 100%;
    cursor: pointer;
    background: rgba(0, 0, 0, .2);
    font-size: 1.5vh;
    transition: all 0.3s ease;

    &:not(.disabled):hover {
        //animation: rotation 0.3s linear;
        background: rgba(0, 0, 0, .4);
        transform: scaleY(1.1);
    }

    &.disabled {
        background: rgba(0, 0, 0, .2);
    }
    
    &.apply {
        background: rgba(111, 240, 108, 0.8);
    }

    &.auth {
        width: 30vw;
        background:rgba(245, 191, 66, .7);
    }

    &.close {
        background:aquamarine;
    }
}

.hide {
    display: none;
}

// breakpoints
@media (max-width:1200px) {
    .qItem, .QmakerContainer {
        min-width: 20vw;
    }
}
@media (max-width:840px) {
    .qItem, .QmakerContainer {
        min-width: 30vw;
    }
    .input_group {
        min-width: 75vw;
    }
}
@media (max-width:700px) {
    .title {
        display: none;
    }
    .profile {
        width: 100%;
    }
    .qItem, .QmakerContainer {
        min-width: 45vw;
    }
    .qList-item {
        flex-wrap: wrap;
        height: 7vh;
        .position {
            min-width: 5%;
        }
        .nameStudent {
            min-width: 94%;
        }
        .vklink {
            min-width: 35%;
        }
        .swap {
            min-width: 50%;
        }
        .btn.delete {
            min-width: 12%;
        }
    }
}
@media (max-width: 400px) {
    .qList-item {
        height: 5vh;
        > * {
            font-size: 0.8em;
        }
    }
}
// breakpoints end
@keyframes countdown {
    from {
        width: 100%;
    }
    to {
        width: 0;
    }
}

body {
    min-height: 100vh;
    background: #E8FFFC;

    .notification {
        position: absolute;
        bottom: 2vh;
        width: 100%;
        animation: fold 0.5s;
        z-index: 1000;

        .notification-content {
            position: relative;
            .countdown {
                position: absolute;
                bottom: 5%;
                left: 0;
                width: 100%;
                height: 2px;
                background: #000;
                animation: countdown 5s ease-in-out;
            }
        }
    }

    .load {
        position: fixed;
        height: 100%;
        width: 100%;
        background-color: rgba(0, 0, 0, .4);

        &::after {
            position: absolute;
            content: "Подождите";
            color: #fff;
            font-weight: 800;
        }

        .square {
            height: 15vh;
            width: 15vh;
            animation: loading 2s ease-in-out infinite;
        }
    }

    .modal {
        visibility: hidden;
        position: fixed;
        width: 100%;
        height: 100%;
        animation: fold 0.5s;
        animation-timing-function: ease;
        background-color: rgba(0, 0, 0, .4);

        &.visible {
            visibility: visible;
        }

        .form {
            flex-direction: column;
            background-color: #fff;

            > * {
                margin-top: 1vh;
            }

            .input_group {
                position: relative;
                width: 30vw;

                input {
                    width: 100%;
                    height: auto;
                    padding: 1vh 0.5vh;
                    box-sizing: border-box;
                    border: 1px solid #000;
                }

                input:focus + .field_name, input:valid + .field_name {
                    font-size: .8em;
                    color: #afbdcf;
                    top: -0.75vh;
                    left:2vh;
                    background: #ffffff;
                    padding: 0px 5px 0px 5px;
                }

                .field_name {
                    position: absolute;
                    font-size: 1em;
                    top: 1vh;
                    left: 0.5vh;
                    pointer-events: none;
                    transition:0.2s ease all;
                }

                input:focus {
                    outline:none;
                }
            }

            .type_selector {
                width: 100%;
                > * {
                    width: 100%;
                    background-color: rgba(0,0,0,0.2);
                    transition: all 0.2s;
                }

                *.active {
                    background-color: rgba(0,0,0,0.4);
                }
                *:hover {
                    background-color: rgba(0,0,0,0.6);
                    transform: scaleY(1.1);
                }
            }

            .smart_options {
                position: relative;
                height: 0;
                overflow-y: hidden;
                transition: all 0.6s;

                &.active {
                    margin: 1vh 0;
                    border-bottom: 2px solid #000;
                    height: auto;

                    .qm_title {
                        width: 100%;
                        position: relative;
                        z-index: 5;
                        overflow: hidden;
                        text-align: center;
                        background-color: #fff;

                        &::after,
                        &::before {
                            content: '';
                            display: inline-block;
                            width: 100%;
                            height: 2px;
                            background: #000;
                            z-index: -1;
                            position: relative;
                            vertical-align: middle;
                        }
                        &::after {
                            right: -0.2vh;
                            margin-right: -100%;
                        }
                        &::before {
                            left: -0.2vh;
                            margin-left: -100%;
                        }

                        .span {
                            pointer-events: none;
                        }

                        .info {
                            width: 2vh;
                            height: 2vh;
                            margin-left: 0.5vw;
                            background-color: gray;
                            border-radius: 50%;
                            color: white;
                        }
                    }
                }
                
                > * {
                    margin: 1vh 0;
                }
            }

            .btn_container {
                width: 100%;
            }
        }

        .content {
            background-color: #fff;
            .info {
                margin-bottom: 2vh;
            }
            .btn.invite {
                background-color: rgba(0, 100, 240, 0.5);
            }
        }

        .notice {
            position: relative;
            margin-top: 1vh;
            width: 90vw;
            background-color: #fff;

            &.update {
                .btn.readMore a {
                    text-decoration: none;
                    color: #000;
                }
            }

            &.apply {
                background: #fff;

                input {
                    margin-top: 1vh;
                    width: 95%;
                }
            }

            &.Ntitle {
                .btn.back {
                    position: absolute;
                    background: url('../assets/back.png') no-repeat;
                    background-size: contain;
                    left: 1%;
                    width: 2.5vw;
                    height: 2.5vw;
                }
            }

            .swap-state {
                .red {
                    color: green;
                }
                .green {
                    color: red;
                }
            }

            &.update {
                justify-content: space-between;
                .btn.readMore {
                    background:rgba(0, 240, 0, 0.5);
                    width: 50%;
                }
            }

            .btn-container {
                margin-top: 1vh;
                justify-content: space-between;
                width: 95%;
                .btn {
                    width: 49%;
                }
            }
        }
    }

    header {
        position: absolute;
        top: 0;
        width: 100%;
        flex-direction: row;
        justify-content: space-between;
        height: 8vh;
        background: #D9D9D9;

        .container {
            box-sizing: border-box;
            height: 5vh;
            .logo {
                box-sizing: border-box;
                height: 5vh;
                width: 5vh;
                margin-right: 1vw;
                transition: all 0.5s;

                &:hover {
                    transform: rotate(1turn);
                }
            }
        }

        .profile {
            box-sizing: border-box;
            justify-content: center;
            position: relative;
            height: 5vh;
            min-width: 30vw;
            .name {
                padding: 0 2vw;
            }
            .notice {
                position: absolute;
                left: 1vw;
                height: 3vh;
                width: 3vh;
                background: url('../assets/notification.png') no-repeat;
                background-size: contain;
                cursor: pointer;

                &::after {
                    content: '';
                    opacity: 0;
                    transition: opacity 0.5s;
                }

                &:hover::after {
                    position: absolute;
                    opacity: 1;
                    content: 'Уведомления';
                    font-size: 0.8em;
                    top: 5vh;
                    right: -3vw;
                    padding: 1vh 1vw;
                    background: rgba(0, 240, 0, 0.5);
                    border: 1px solid #000;
                }
            }
            .logout {
                position: absolute;
                right: 1vw;
                height: 3vh;
                width: 3vh;
                background: url('../assets/logout.png') no-repeat;
                background-size: contain;
                cursor: pointer;

                &::after {
                    content: '';
                    opacity: 0;
                    transition: opacity 0.5s;
                }

                &:hover::after {
                    position: absolute;
                    opacity: 1;
                    content: 'Выход';
                    font-size: 0.8em;
                    top: 5vh;
                    right: -1.2vw;
                    padding: 1vh 1vw;
                    background: rgba(255, 0, 0, 0.5);
                    border: 1px solid #000;
                }
            }
        }
    }

    main {
        position: absolute;
        top : 8vh;
        width: 100%;
        animation: fold 0.5s;
        animation-timing-function: ease;


        .qItem, .QmakerContainer {
            position: relative;
            padding: 1vh 1vw;
            margin: 1vh 1vw;
            box-sizing: border-box;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: center;
            width: 15vw;
            height: 40vh;
        }

        .qItem, 
        .QmakerContainer {
            &::after {
                content: "";
                bottom: 0;
                position: absolute;
                width: 100%;
                height: 0;
                background: rgba(245, 191, 66, .7);
                //background: url('../assets/beer.jpg') no-repeat center center;
                background-size: cover;
                transition: height 0.5s;
                z-index: -1;
            }
        }

        .qItem:hover, 
        .QmakerContainer:hover {
            &::after {
                height: 100%;
            }
        }

        .QmakerContainer {
            display: flex;
            justify-content: center;
            align-items: center;

            .btn.about {
                background-color: rgba(10, 200, 200, 0.7);
            }
        }

        .qList {
            width: 100%;

            .subject-name {
                position: relative;
                height: 5vh;

                .btn.back {
                    position: absolute;
                    background: url('../assets/back.png') no-repeat center;
                    background-size: contain;
                    left: 1%;
                    width: 4vh;
                    height: 4vh;
                    background-color: rgba(200, 10, 0, 0.3);
                }

                .btn_container {
                    position: absolute;
                    justify-content: space-between;
                    width: 7vh;
                    right: 1%;
                    .btn.copy {
                        background: url('../assets/copy.png') no-repeat center;
                        background-size: contain;
                        width: 3vh;
                        height: 3vh;
                    }
    
                    .btn.exit {
                        background: url('../assets/logout.png') no-repeat center;
                        background-size: contain;
                        width: 3vh;
                        height: 3vh;
                    }
                }
            }

            .contanier {
                margin-top: 2vh;
                width: 100%;
                height: 81vh;
                overflow-y: overlay;
                .qList-item {
                    width: 100%;
                    margin-bottom: 1vh;
                    justify-content: space-between;

                    &.uitem {
                        height: 3vh;
                    }
                    > * {
                        box-sizing: border-box;
                        padding: 0.3vw;
                    }
                    .position {
                        width: 5%;
                    }
                    .nameStudent {
                        width: 45%;
                    }
                    .vklink {
                        width: 20%;
                        text-decoration: none;
                        background-color: rgba(0, 30, 200, .3);
                    }
                    .swap {
                        width: 20%;
                        cursor: pointer;
                    }
                    .btn.delete {
                        width: 5%;
                        background:rgba(255, 0, 0, 0.5);
                    }
                    .u {
                        width: 93.7%;
                        background-color: rgba(0, 200, 200, .3);
                    }
                }
            }
        }
    }
}

@keyframes rotation {
    0% {
        transform: rotate(5deg);
    }
    50% {
        transform: rotate(-5deg);
    }
    100%{
        transform: rotate(0deg);
    }
}

@keyframes fold {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
}

@keyframes loading {
    0% {
        transform: rotate(180deg);
    }
    50% {
        transform: rotate(-360deg) scale(0.6);
    }
    100% {
        transform: rotate(0deg)
    }
}
